import _mapValues from "lodash/mapValues";
import _minBy from "lodash/minBy";
import _maxBy from "lodash/maxBy";
import _countBy from "lodash/countBy";
import _groupBy from "lodash/groupBy";
import Checktainer from "./checktainer.js";
import Orchestrator from "./orchestrator.js";
import * as dbMock from "./db-mock.js";
import * as sqsMock from "./sqs-mock.js";
import Enqueuer from "./enqueuer.js";
import { delay } from "./delay-mock.js";
import { getQueue } from "./sqs-mock.js";

export default class PlatformMock {
  running = false;

  orchestrators = [new Orchestrator(), new Orchestrator()];
  checktainer = new Checktainer();
  enqueuer = new Enqueuer();

  constructor() {}

  addWorkflows(list) {
    dbMock.workflows.push(...list.map((d) => new dbMock.WorkflowModel(d)));
  }

  setNumOfOrchestrators(num) {
    if (this.orchestrators.length < num) {
      for (let i = this.orchestrators.length; i < num; i++) {
        const o = new Orchestrator();
        this.orchestrators.push(o);
        if (this.running) {
          o.run();
        }
      }
    } else {
      for (let i = this.orchestrators.length; i > num; i--) {
        this.orchestrators.pop().stop();
      }
    }
  }

  setMaxWorkflowsPerOrchestrator(max) {
    Orchestrator.runningWorkflowsMax = max;
  }

  async run() {
    for (const orchestrator of this.orchestrators) {
      orchestrator.run();
    }
    this.checktainer.run();
    this.enqueuer.run();

    this.running = true;
    console.log("Platform started");

    while (this.running) {
      await delay(1000);
    }

    for (const orchestrator of this.orchestrators) {
      orchestrator.stop();
    }
    this.checktainer.stop();
    this.enqueuer.stop();

    console.log("Platform stopped");
  }

  stop() {
    this.running = false;
  }

  getStats(pipelineId) {
    const pipelineWorkflows = dbMock.workflows.filter(
      (wf) => wf.pipelineId === pipelineId
    );
    const pipelineTasks = dbMock.tasks.filter(
      (t) => t.workflow.pipelineId === pipelineId
    );
    const pipelineContainers = dbMock.containers.filter(
      (c) => c.pipelineId === pipelineId
    );

    const durations = pipelineWorkflows
      .filter((wf) => wf.status === "completed")
      .map((wf) => ({
        duration:
          new Date(wf.lastUpdatedAt).getTime() -
          new Date(wf.startedAt).getTime(),
        waiting:
          new Date(wf.startedAt).getTime() - new Date(wf.createdAt).getTime()
      }));

    return {
      workflows: _countBy(pipelineWorkflows, "status"),
      tasks: _countBy(pipelineTasks, "status"),
      containers: _countBy(pipelineContainers, "status"),
      idlingContainers: pipelineContainers.filter(
        (c) => c.status === "running" && !c.runningTask
      ).length,
      inQueue: sqsMock
        .getQueue("enqueuer2Orchestrator")
        .filter((wf) => wf.pipelineId === pipelineId).length,
      times: durations.length
        ? {
            durationMin: _minBy(durations, "duration").duration / 1000,
            durationMax: _maxBy(durations, "duration").duration / 1000,
            waitingMin: _minBy(durations, "waiting").waiting / 1000,
            waitingMax: _maxBy(durations, "waiting").waiting / 1000
          }
        : {}
    };
  }

  get state() {
    return {
      workflowsByStatus: _groupBy(dbMock.workflows, "status"),
      orchestrators: this.orchestrators.map((o) => ({
        id: o.id,
        workflows: dbMock.workflows.filter(
          (wf) =>
            wf.orchestratorId === o.id &&
            ["pending", "queued", "in-progress"].includes(wf.status)
        )
      })),
      execQueue: sqsMock.getQueue("enqueuer2Orchestrator"),
      containers: dbMock.containers.filter((c) =>
        ["creating", "running"].includes(c.status)
      )
    };
  }

  getStartedWorkflows() {
    return dbMock.workflows.filter((wf) => wf.startedAt);
  }

  getStartedTasks() {
    return dbMock.tasks.filter((t) => t.startedAt && t.container);
  }

  getContainers() {
    return dbMock.containers;
  }
}
