import _groupBy from "lodash/groupBy";
import _orderBy from "lodash/orderBy";
import { delay } from "./delay-mock.js";
import * as dbMock from "./db-mock.js";
import Orchestrator from "./orchestrator.js";

export default class Scheduler {
  running = false;

  maxOrchestrators = 8;

  /**
   * @type {PlatformMockNew}
   */
  platform = null;

  noScaleUpCounter = 0;

  constructor(platform) {
    this.platform = platform;
  }

  getMaxWorkflowsPerPipeline(activePipelinesCount = 1, maxCapacity) {
    return Math.ceil(maxCapacity / activePipelinesCount); // limits?
  }

  async run() {
    this.running = true;
    console.log("Scheduler started");

    while (this.running) {
      const currentMaxCapacity =
        Orchestrator.runningWorkflowsMax * this.platform.orchestrators.length;

      const pendingAndRunning = dbMock.workflows.filter((wf) =>
        ["pending", "in-progress"].includes(wf.status)
      );

      const wfGroups = Object.values(_groupBy(pendingAndRunning, "pipelineId"));

      if (wfGroups.length) {
        const maxWorkflowsPerPipeline = this.getMaxWorkflowsPerPipeline(
          wfGroups.length,
          currentMaxCapacity
        );

        let exitLoop = false;
        while (!exitLoop) {
          exitLoop = true;
          for (const pipelineWorkflows of wfGroups) {
            const allocatedForPipelineCount = pipelineWorkflows.filter(
              (wf) => wf.orchestratorId
            ).length;
            if (allocatedForPipelineCount < maxWorkflowsPerPipeline) {
              const firstNotAllocatedWF = pipelineWorkflows.find(
                (wf) => !wf.orchestratorId
              );
              if (!firstNotAllocatedWF) continue;
              const sortedOrchestrators = _orderBy(
                this.platform.orchestrators,
                (o) =>
                  dbMock.workflows.filter(
                    (wf) =>
                      ["pending", "in-progress"].includes(wf.status) &&
                      wf.orchestratorId === o.id
                  ).length
              );
              for (const o of sortedOrchestrators) {
                const allocatedCount = dbMock.workflows.filter(
                  (wf) =>
                    ["pending", "in-progress"].includes(wf.status) &&
                    wf.orchestratorId === o.id
                ).length;
                if (allocatedCount < Orchestrator.runningWorkflowsMax) {
                  firstNotAllocatedWF.orchestratorId = o.id;
                  exitLoop = false;
                  break;
                }
              }
            }
          }
        }
      }

      const allocatedCount = dbMock.workflows.filter(
        (wf) =>
          ["pending", "in-progress"].includes(wf.status) && wf.orchestratorId
      ).length;
      const notAllocatedWorkflows = dbMock.workflows.filter(
        (wf) => !wf.orchestratorId
      );
      const notAllocatedCount = notAllocatedWorkflows.length;

      // TODO: rethink scaling...
      if (
        notAllocatedCount > 0 &&
        allocatedCount + notAllocatedCount > currentMaxCapacity &&
        allocatedCount > 0.8 * currentMaxCapacity
      ) {
        const [firstWaitingWorkflow] = notAllocatedWorkflows;
        const ageOfFirstWaitingWorkflow =
          Date.now() - new Date(firstWaitingWorkflow.createdAt).getTime();
        if (
          ageOfFirstWaitingWorkflow > 30000 &&
          this.platform.orchestrators.length < this.maxOrchestrators &&
          this.noScaleUpCounter === 0
        ) {
          this.platform.incNumOfOrchestrators();
          this.noScaleUpCounter = 10;
        }
      }

      if (this.noScaleUpCounter > 0) this.noScaleUpCounter--;

      await delay(1000);
    }
    console.log("Scheduler stopped");
  }

  stop() {
    this.running = false;
  }
}
