import _groupBy from "lodash/groupBy";
import * as dbMock from "./db-mock.js";
import { delay } from "./delay-mock.js";
import WorkerInstance from "./worker-instance.js";

export default class Checktainer {
  running = false;

  async run() {
    this.running = true;
    console.log("Checktainer started");
    while (this.running) {
      const grouped = _groupBy(
        dbMock.tasks.filter((t) => t.status === "pending"),
        "taskGroupHash"
      );
      for (const tgHash in grouped) {
        await this.ensureGroup(tgHash);
      }

      await delay(1000);
    }
    console.log("Checktainer stopped");
  }

  stop() {
    this.running = false;
  }

  calcDesiredCreateCount(p) {
    if (p.pendingTasksCount < 1) {
      return 0;
    }

    // wait until there are no containers in creation state
    if (p.numOfNonStoppedContainers > p.numOfRunningContainers) {
      return 0;
    }

    // initial creation
    if (p.numOfNonStoppedContainers === 0 && p.pendingTasksCount > 0) {
      return p.initNumOfWorkersToCreate;
    }

    // we have more running containers than running tasks, low utilization?
    if (p.numOfRunningContainers > p.runningTasksCount) {
      return 0;
    }

    // all containers are busy... give me more!
    return p.numOfWorkersToInc;
  }

  async ensureGroup(taskGroupHash) {
    const taskGroupItems = dbMock.tasks.filter(
      (t) =>
        t.taskGroupHash === taskGroupHash &&
        ["pending", "in-progress"].includes(t.status)
    );

    // borrow logic from ts-demo-p2
    const containers = dbMock.ContainerModel.listForTaskGroupHash(
      taskGroupHash,
      ["creating", "running"]
    );

    // start a new container
    const [firstPendingTask] = taskGroupItems.filter(
      (t) => t.status === "pending"
    );
    const pendingTasksCount = taskGroupItems.filter(
      (t) => t.status === "pending"
    ).length;
    const runningTasksCount = taskGroupItems.filter(
      (t) => t.status === "in-progress"
    ).length;

    const numOfNonStoppedContainers = containers.length;
    const numOfRunningContainers = containers.filter(
      (c) => c.status === "running"
    ).length;

    const desiredCreateCount = this.calcDesiredCreateCount({
      pendingTasksCount,
      runningTasksCount,
      numOfRunningContainers,
      numOfNonStoppedContainers,
      initNumOfWorkersToCreate: 1,
      numOfWorkersToInc: 1
    });
    let desiredCount = desiredCreateCount + runningTasksCount;

    const createCount = desiredCount - numOfNonStoppedContainers;

    for (let i = 0; i < createCount; i++) {
      const dbContainer = new dbMock.ContainerModel({
        taskGroupHash,
        type: firstPendingTask.type,
        pipelineId: firstPendingTask.workflow.pipelineId
      });
      dbMock.containers.push(dbContainer);
      new WorkerInstance(dbContainer);
    }
  }
}
