import { delay } from "./delay-mock.js";
import * as dbMock from "./db-mock.js";

export default class WorkerInstance {
  static startDelay = {
    ecs: 5000,
    windows: 30000
  };

  static maxEmptyPolls = 100;

  dbContainer = null;
  type = "ecs";

  emptyPolls = 0;

  constructor(dbContainer) {
    this.dbContainer = dbContainer;
    this.init();
  }

  async init() {
    await delay(WorkerInstance.startDelay[this.dbContainer.type]);
    this.run();
  }

  async run() {
    this.dbContainer.startedAt = new Date().toISOString();
    this.dbContainer.status = "running";
    while (this.dbContainer.status === "running") {
      const task = this.pollNewTask();
      if (task) {
        this.dbContainer.runningTask = task;
        task.container = this.dbContainer;
        let progressIterations = task.duration / 1000;
        for (let i = 0; i < progressIterations; i++) {
          await delay(1000);
          task.progress = i / progressIterations;
        }
        task.progress = 1;
        task.status = "completed";
        this.dbContainer.runningTask = null;
        this.emptyPolls = 0;
        this.dbContainer.reuseCount++;
      } else {
        this.emptyPolls++;
        if (this.emptyPolls > WorkerInstance.maxEmptyPolls) {
          this.stop();
        }
      }
      await delay(500);
    }
  }

  stop() {
    this.dbContainer.status = "stopped";
  }

  pollNewTask() {
    return dbMock.TaskModel.poll(this.dbContainer.taskGroupHash);
  }
}
