import _groupBy from "lodash/groupBy";

export const workflows = [];
export const tasks = [];
export const containers = [];

export class WorkflowModel {
  static idCounter = 1;

  static getNForEnqueuer(n) {
    const list = [];

    const allPending = workflows.filter((wf) => wf.status === "pending");

    const wfGroups = Object.values(_groupBy(allPending, "pipelineId"));

    let exitLoop = false;
    while (!exitLoop) {
      exitLoop = true;
      for (let pipelineWorkflows of wfGroups) {
        const wf = pipelineWorkflows.find((wf) => wf.status === "pending");
        if (wf) {
          wf.status = "queued";
          list.push(wf);
          exitLoop = false;
        }
        if (list.length >= n) return list;
      }
    }
    return list;
  }

  static getPendingForTheOrchestrator(orchestratorId) {
    return workflows.filter(
      (wf) => wf.status === "pending" && wf.orchestratorId === orchestratorId
    );
  }

  id = null;
  orchestratorId = null;
  _status = "pending";
  createdAt = null;
  lastUpdatedAt = null;
  startedAt = null;
  orgSlug = null;
  pipelineId = null;
  pipelineConfig = {};
  protocol = null; // { id: 'someid', steps: [{type: 'ecs', duration: 2000}, ...] }
  tasks = [];

  constructor(data) {
    this.id = data.pipelineId + "-" + WorkflowModel.idCounter++;
    this.createdAt = new Date().toISOString();
    this.lastUpdatedAt = new Date().toISOString();
    Object.assign(this, data);
  }

  set status(v) {
    this._status = v;
    this.lastUpdatedAt = new Date().toISOString();
  }

  get status() {
    return this._status;
  }
}

export class TaskModel {
  static idCounter = 1;

  static poll(tgHash) {
    const [firstPendingTask] = tasks.filter(
      (t) => t.taskGroupHash === tgHash && t.status === "pending"
    );
    if (firstPendingTask) {
      firstPendingTask.startedAt = new Date().toISOString();
      firstPendingTask.status = "in-progress";
      return firstPendingTask;
    }
    return null;
  }

  id = null;
  workflow = null;
  taskScript = null;
  taskGroupHash = null;
  type = "ecs"; // ecs or windows
  duration = 5000;
  _status = "pending";
  createdAt = null;
  startedAt = null;
  lastUpdatedAt = null;
  container = null;

  constructor(data) {
    this.id = TaskModel.idCounter++;
    this.createdAt = new Date().toISOString();
    this.lastUpdatedAt = new Date().toISOString();
    Object.assign(this, data);
  }

  set status(v) {
    this._status = v;
    this.lastUpdatedAt = new Date().toISOString();
  }

  get status() {
    return this._status;
  }
}

export class ContainerModel {
  static idCounter = 1;

  static listForTaskGroupHash(tgHash, statuses) {
    return containers.filter(
      (c) => c.taskGroupHash === tgHash && statuses.includes(c.status)
    );
  }

  id = null;
  taskGroupHash = null;
  pipelineId = null;
  type = "ecs"; // ecs or windows
  duration = 5000;
  progress = 0;
  _status = "creating";
  createdAt = null;
  startedAt = null;
  lastUpdatedAt = null;
  runningTask = null;
  reuseCount = 0;
  memoryInMB = 512;

  constructor(data) {
    this.id = TaskModel.idCounter++;
    this.createdAt = new Date().toISOString();
    this.lastUpdatedAt = new Date().toISOString();
    Object.assign(this, data);
  }

  set status(v) {
    this._status = v;
    this.lastUpdatedAt = new Date().toISOString();
  }

  get status() {
    return this._status;
  }
}
