import * as dbMock from "./db-mock.js";
import { getQueueSize, addToQueue } from "./sqs-mock.js";
import { delay } from "./delay-mock.js";

export default class Enqueuer {
  execQueueMaxLength = 50;

  running = false;

  async run() {
    this.running = true;
    console.log("Enqueuer started");
    while (this.running) {
      const messagesToPush =
        this.execQueueMaxLength - getQueueSize("enqueuer2Orchestrator");
      if (messagesToPush > 0) {
        const messages = dbMock.WorkflowModel.getNForEnqueuer(messagesToPush);
        addToQueue("enqueuer2Orchestrator", messages.reverse());
      }
      await delay(1000);
    }
    console.log("Enqueuer stopped");
  }

  stop() {
    this.running = false;
  }

  setQueueMaxLength(max) {
    this.execQueueMaxLength = max;
  }
}
