import * as sqsMock from "./sqs-mock.js";
import * as dbMock from "./db-mock.js";
import { delay } from "./delay-mock.js";

export default class Orchestrator {
  static idCounter = 1;
  static runningWorkflowsMax = 10;
  static startDelay = 5000;

  id = null;
  running = false;
  newMode = false;

  reserved = false;

  runningWorkflows = new Set([]);

  idlingStartedTimestamp = null;

  constructor(newMode = false, reserved = false) {
    this.id = Orchestrator.idCounter++;
    this.newMode = newMode;
    this.reserved = reserved;
  }

  async run() {
    await delay(Orchestrator.startDelay);
    this.idlingStartedTimestamp = Date.now();
    if (this.newMode) {
      return this.runNew();
    } else {
      return this.runOld();
    }
  }

  async runOld() {
    this.running = true;
    console.log(`Orchestrator ${this.id} started`);
    while (this.running) {
      if (this.runningWorkflows.size < Orchestrator.runningWorkflowsMax) {
        const workflowsToExec = sqsMock.popFromQueue(
          "enqueuer2Orchestrator",
          Orchestrator.runningWorkflowsMax - this.runningWorkflows.size
        );
        for (const wf of workflowsToExec) {
          this.runningWorkflows.add(wf);
          this.runWorkflow(wf);
          if (!this.running) break;
        }
      }
      await delay(1000);
    }
    console.log(`Orchestrator ${this.id} stopped`);
  }

  async runNew() {
    this.running = true;
    console.log(`Orchestrator ${this.id} started (new mode)`);
    while (this.running) {
      const workflowsToExec = dbMock.WorkflowModel.getPendingForTheOrchestrator(
        this.id
      );
      for (const wf of workflowsToExec) {
        this.idlingStartedTimestamp = null;
        this.runningWorkflows.add(wf);
        this.runWorkflow(wf);
        if (!this.running) break;
      }
      await delay(1000);
    }
    console.log(`Orchestrator ${this.id} stopped`);
  }

  stop() {
    this.running = false;
  }

  async runWorkflow(wf) {
    wf.orchestratorId = this.id;
    wf.status = "in-progress";
    wf.startedAt = new Date().toISOString();

    for (const step of wf.protocol.steps) {
      await this.runTask(wf, step);
    }
    wf.status = "completed";
    this.runningWorkflows.delete(wf);
    if (!this.runningWorkflows.size) {
      this.idlingStartedTimestamp = Date.now();
    }
  }

  async runTask(wf, protocolStep) {
    const dbTask = new dbMock.TaskModel({
      workflow: wf,
      image: protocolStep.image,
      taskGroupHash: [
        wf.orgSlug,
        wf.pipelineId,
        protocolStep.type,
        protocolStep.taskScript
      ].join("-"),
      type: protocolStep.type,
      duration: protocolStep.duration
    });
    dbMock.tasks.push(dbTask);
    wf.tasks.push(dbTask);

    // monitor task...
    while (["pending", "in-progress"].includes(dbTask.status)) {
      await delay(1000);
    }
  }

  get idlingDuration() {
    return this.idlingStartedTimestamp
      ? Date.now() - this.idlingStartedTimestamp
      : 0;
  }
}
