import React, { useEffect, useState, useRef } from "react";
import PlatformMock from "./platform-mock.js";
import * as qs from "query-string";
import PlatformMockNew from "./platform-mock-new.js";
import {
  Button,
  Table,
  TableHeader,
  TableBody,
  TableHeaderCell,
  TableRow,
  TableCell,
  Label,
  Icon,
  Segment,
  Input,
  Progress,
  Checkbox
} from "semantic-ui-react";
import * as delayMock from "./delay-mock.js";
import _times from "lodash/times.js";
import _random from "lodash/random.js";
import _orderBy from "lodash/orderBy.js";
import { Timeline, DataSet } from "vis-timeline/standalone";

let platform,
  timeline,
  timelineContainersMode = 0;

const PipelinesSimulation = () => {
  const { old: oldVersion } = qs.parse(location.search);
  const newVersion = !oldVersion;

  if (!platform) {
    platform = newVersion ? new PlatformMockNew() : new PlatformMock();
  }

  const timelineRef = useRef(null);
  const [running, setRunning] = useState(false);
  const [stats, setStats] = useState(null);
  const [platformState, setPlatformState] = useState(null);
  const [speed, setSpeed] = useState(1);
  const [numOfOrchestrators, setNumOfOrchestrators] = useState(2);
  const [workflowsPerOrchestrator, setWorkflowsPerOrchestrator] = useState(10);
  const [queueSize, setQueueSize] = useState(50);
  const [workflowsToAdd, setWorkflowsToAdd] = useState(100);

  const startSimulation = async () => {
    setRunning(true);
    platform.run();
  };

  const stopSimulation = async () => {
    platform.stop();
    setRunning(false);
    await syncWithPlatform();
  };

  const syncWithPlatform = async () => {
    setStats({
      black: platform.getStats("black"),
      red: platform.getStats("red"),
      blue: platform.getStats("blue")
    });
    setPlatformState(platform.state);
    setNumOfOrchestrators(platform.orchestrators.length);

    if (!timeline) {
      timeline = new Timeline(timelineRef.current, [], new DataSet([]), {
        orientation: {
          axis: "both",
          item: "top"
        },
        groupOrder: "index",
        zoomFriction: 20,
        preferZoom: false,
        stack: false,
        groupHeightMode: "fixed",
        tooltip: {
          followMouse: true,
          delay: 100
        },
        horizontalScroll: true,
        verticalScroll: true,
        zoomKey: "ctrlKey",
        start: new Date(),
        end: new Date(Date.now() + 300000)
      });
    } else if (platform.running && timelineContainersMode) {
      const tasksWithContainers = platform.getStartedTasks();

      const timelineItems = tasksWithContainers.map((t) => ({
        id: t.id,
        group: t.container.id,
        title: `task-${t.id}`,
        content: `task-${t.id}`,
        start: new Date(t.startedAt),
        end: t.status !== "completed" ? new Date() : new Date(t.lastUpdatedAt),
        type: "range",
        selectable: false,
        style: `border-color: black; background-color: ${t.workflow.pipelineId}; opacity: 0.8; color: white;`
      }));

      const containers = platform.getContainers();

      timelineItems.push(
        ...containers.map((c) => ({
          id: `c-${c.id}`,
          group: c.id,
          start: new Date(c.createdAt),
          end: c.status !== "stopped" ? new Date() : new Date(c.lastUpdatedAt),
          type: "background",
          selectable: false,
          style: `background-color: ${c.pipelineId}; opacity: 0.3;`
        }))
      );

      const timelineGroups = new DataSet(
        containers.map((c, index) => ({
          id: c.id,
          index,
          content: `<small>${c.type === "ecs" ? "ecs" : "win"} ${
            c.memoryInMB
          }MB</small>`,
          style: `color: ${c.pipelineId};`
        }))
      );

      timeline.setData({
        groups: timelineGroups,
        items: timelineItems
      });
    } else {
      const wfList = _orderBy(platform.getStartedWorkflows(), (wf) =>
        new Date(wf.startedAt).getTime()
      );

      const timelineItems = wfList.map((wf) => ({
        id: wf.id,
        group: wf.id,
        title: wf.id,
        content: wf.id,
        start: new Date(wf.startedAt),
        end:
          wf.status !== "completed" ? new Date() : new Date(wf.lastUpdatedAt),
        type: "background",
        selectable: false,
        style: `border-color: black; background-color: ${wf.pipelineId}; opacity: 0.8; color: white;`
      }));

      timelineItems.push(
        ...wfList.map((wf) => ({
          id: `${wf.id}-created`,
          group: wf.id,
          title: wf.id,
          start: new Date(wf.createdAt),
          type: "point",
          selectable: false,
          className: `timeline-point-${wf.pipelineId}`
        }))
      );

      timelineItems.push(
        ...wfList.reduce((acc, wf) => {
          acc.push(
            ...wf.tasks
              .filter((t) => t.startedAt)
              .map((t) => ({
                id: `task-${t.id}`,
                group: wf.id,
                title: `${t.id}`,
                // content: `${t.id}`,
                start: new Date(t.startedAt),
                end:
                  t.status !== "completed"
                    ? new Date()
                    : new Date(t.lastUpdatedAt),
                type: "range",
                selectable: false,
                style: `border-color: white; background: none; color: white;`
              }))
          );
          return acc;
        }, [])
      );

      const timelineGroups = new DataSet(
        wfList.map((wf, index) => ({
          id: wf.id,
          index,
          content: ""
        }))
      );

      timeline.setData({
        groups: timelineGroups,
        items: timelineItems
      });
    }
  };

  const changeSpeed = (event) => {
    setSpeed(event.target.value);
    delayMock.setSpeed(1 * event.target.value || 1);
  };

  const changeNumOfOrchestrators = (event) => {
    platform.setNumOfOrchestrators(1 * event.target.value || 1);
    setNumOfOrchestrators(platform.orchestrators.length);
  };

  const changeWorkflowsPerOrchestrator = (event) => {
    setWorkflowsPerOrchestrator(event.target.value);
    platform.setMaxWorkflowsPerOrchestrator(1 * event.target.value || 1);
  };

  const changeQueueSize = (event) => {
    setQueueSize(event.target.value);
    platform.enqueuer.setQueueMaxLength(1 * event.target.value || 1);
  };

  const changeWorkflowsToAdd = (event) => {
    setWorkflowsToAdd(event.target.value);
  };

  const addWorkflows = (pipelineIds) => {
    const getPipelineIdToSteps = () => ({
      red: [
        { type: "ecs", duration: 1000 * _random(1, 8), taskScript: "ts1" },
        { type: "windows", duration: 5000 * _random(1, 5), taskScript: "ts2" }
      ],
      blue: [
        { type: "ecs", duration: 1000 * _random(1, 5), taskScript: "ts4" }
      ],
      black: [
        { type: "ecs", duration: 1000 * _random(100, 200), taskScript: "ts5" }
      ]
    });
    platform.addWorkflows(
      _times(workflowsToAdd, (i) => {
        const pipelineId = pipelineIds[_random(0, pipelineIds.length - 1)];
        return {
          orgSlug: "st-org",
          pipelineId: pipelineId,
          protocol: {
            id: "someid",
            steps: getPipelineIdToSteps()[pipelineId]
          }
        };
      })
    );
  };
  const containerOSIcon = (c) => (
    <>
      {c.type === "windows" && (
        <Icon
          name="windows"
          className="pull-right"
          color={c.pipelineId}
          disabled
        />
      )}
      {c.type === "ecs" && (
        <Icon
          name="linux"
          className="pull-right"
          color={c.pipelineId}
          disabled
        />
      )}
    </>
  );

  useEffect(() => {
    const syncInterval = setInterval(syncWithPlatform, 1000);
    syncWithPlatform();
    return () => {
      clearInterval(syncInterval);
      if (timeline) {
        timeline.destroy();
        timeline = null;
      }
    };
  }, []);

  return (
    <div style={{ padding: "14px 62px" }}>
      <style>{`
       .timeline-point-blue { border-color: blue; }
       .timeline-point-red { border-color: red; }
       .timeline-point-black { border-color: black; }
      `}</style>
      <h3>
        {newVersion ? "New" : "Old"} architecture simulation (
        {newVersion ? (
          <a href="./?old=1" target="_self">
            switch to old
          </a>
        ) : (
          <a href="./">switch to new</a>
        )}
        )
      </h3>
      <div>
        <Button
          disabled={running}
          loading={running}
          onClick={() => startSimulation()}
        >
          RUN
        </Button>
        &nbsp;
        <Button disabled={!running} onClick={() => stopSimulation()}>
          STOP
        </Button>
        <Input
          type="number"
          size="mini"
          label="speed"
          min="1"
          max="100"
          step="1"
          value={speed}
          onChange={changeSpeed}
          style={{ marginLeft: "30px" }}
        />
        <Input
          type="number"
          size="mini"
          label="orchestrators"
          min="1"
          max="8"
          step="1"
          value={numOfOrchestrators}
          onChange={changeNumOfOrchestrators}
          style={{ marginLeft: "4px" }}
        />
        <Input
          type="number"
          size="mini"
          label="workflows per orchestrator"
          min="1"
          max="100"
          step="1"
          value={workflowsPerOrchestrator}
          onChange={changeWorkflowsPerOrchestrator}
          style={{ marginLeft: "4px" }}
        />
        {!newVersion && (
          <Input
            type="number"
            size="mini"
            label="queue size"
            min="5"
            max="1000"
            step="5"
            value={queueSize}
            onChange={changeQueueSize}
            style={{ marginLeft: "4px" }}
          />
        )}
        <Button
          className="pull-right"
          style={{ marginLeft: "14px" }}
          onClick={() => addWorkflows(["blue", "red", "black"])}
        >
          +mix
        </Button>
        <Button
          className="pull-right"
          color="blue"
          onClick={() => addWorkflows(["blue"])}
        >
          +
        </Button>
        <Button
          className="pull-right"
          color="red"
          onClick={() => addWorkflows(["red"])}
        >
          +
        </Button>
        <Button
          className="pull-right"
          color="black"
          onClick={() => addWorkflows(["black"])}
        >
          +
        </Button>
        <Input
          type="number"
          label="to create"
          min="1"
          max="100"
          step="1"
          value={workflowsToAdd}
          onChange={changeWorkflowsToAdd}
          className="pull-right"
          style={{ marginRight: "8px" }}
        />
      </div>
      <div style={{ marginTop: "10px" }}>
        {platformState && (
          <Table>
            <TableHeader>
              <TableRow>
                <TableHeaderCell width="1">
                  Pending [
                  {(platformState.workflowsByStatus.pending || []).length}]
                </TableHeaderCell>
                {!newVersion && (
                  <TableHeaderCell width="1">Queue</TableHeaderCell>
                )}
                {platformState.orchestrators.map((o, i) => (
                  <TableHeaderCell width="2" key={o.id}>
                    Orch #{i + 1} [{o.workflows.length}]
                  </TableHeaderCell>
                ))}
                <TableHeaderCell width="2">
                  Containers [{platformState.containers.length}]
                </TableHeaderCell>
                <TableHeaderCell width="2">Stats</TableHeaderCell>
              </TableRow>
            </TableHeader>
            <TableBody>
              <TableRow>
                <TableCell verticalAlign="top">
                  {(platformState.workflowsByStatus.pending || []).map((wf) => (
                    <div key={wf.id} style={{ margin: "2px" }}>
                      <Label size="tiny" color={wf.pipelineId}>
                        {wf.id}
                      </Label>
                    </div>
                  ))}
                </TableCell>
                {!newVersion && (
                  <TableCell verticalAlign="top">
                    {(platformState.execQueue || []).map((wf) => (
                      <div key={wf.id} style={{ margin: "2px" }}>
                        <Label size="tiny" color={wf.pipelineId}>
                          {wf.id}
                        </Label>
                      </div>
                    ))}
                  </TableCell>
                )}
                {platformState.orchestrators.map((o) => (
                  <TableCell verticalAlign="top" key={o.id}>
                    {o.workflows.map((wf) => (
                      <div key={wf.id}>
                        <Segment.Group horizontal size="tiny">
                          {wf.tasks.map((t) => (
                            <Segment compact color={wf.pipelineId} key={t.id}>
                              {wf.id}-{t.id}
                              {t.status === "pending" && (
                                <Icon
                                  name="hourglass"
                                  className="pull-right"
                                  color={wf.pipelineId}
                                  disabled
                                />
                              )}
                              {t.status === "in-progress" && (
                                <Progress
                                  percent={t.progress * 100}
                                  size="tiny"
                                  style={{ margin: "4px 0 0" }}
                                  color={wf.pipelineId}
                                ></Progress>
                              )}
                            </Segment>
                          ))}
                        </Segment.Group>
                      </div>
                    ))}
                  </TableCell>
                ))}
                <TableCell verticalAlign="top">
                  {(platformState.containers || []).map((c) =>
                    c.status === "creating" ? (
                      <Segment key={c.id} color={c.pipelineId}>
                        Creating {c.type}...
                        {containerOSIcon(c)}
                      </Segment>
                    ) : c.runningTask ? (
                      <Segment color={c.pipelineId} key={c.id}>
                        {c.runningTask.workflow.id}-{c.runningTask.id} [
                        {c.reuseCount}]{containerOSIcon(c)}
                        <Progress
                          percent={c.runningTask.progress * 100}
                          size="tiny"
                          style={{ margin: "4px 0 0" }}
                          color={c.pipelineId}
                        ></Progress>
                      </Segment>
                    ) : (
                      <Segment color={c.pipelineId} key={c.id}>
                        Idling [{c.reuseCount}]{containerOSIcon(c)}
                      </Segment>
                    )
                  )}
                </TableCell>
                <TableCell verticalAlign="top">
                  {["black", "red", "blue"].map((pipelineId) => (
                    <Segment color={pipelineId} key={pipelineId}>
                      <pre style={{ color: pipelineId }}>
                        {JSON.stringify(stats[pipelineId], null, 2)}
                      </pre>
                    </Segment>
                  ))}
                </TableCell>
              </TableRow>
            </TableBody>
          </Table>
        )}
      </div>
      <div style={{ marginTop: "10px" }}>
        <Checkbox
          toggle
          label={
            timelineContainersMode
              ? "Containers timeline"
              : "Workflows timeline"
          }
          onChange={() => {
            timelineContainersMode = 1 - timelineContainersMode;
            syncWithPlatform();
          }}
          value={timelineContainersMode}
        />
      </div>
      <div ref={timelineRef} style={{ marginTop: "10px" }} />
    </div>
  );
};

PipelinesSimulation.propTypes = {};

export default PipelinesSimulation;
