const queues = {
  enqueuer2Orchestrator: []
};

export const addToQueue = (queueId, messages) => {
  queues[queueId].unshift(...messages);
};

export const popFromQueue = (queueId, limit = 10) => {
  const messages = [];
  while (queues[queueId].length && messages.length < limit) {
    messages.push(queues[queueId].pop());
  }
  return messages;
};

export const getQueue = (queueId) => queues[queueId];

export const getQueueSize = (queueId) => queues[queueId].length;
